import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IInstitutionModel, IInstitutionRequest } from "../models/institution";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class InstitutionService extends AppClientService {
	constructor() {
		super();
	}

	create(body: IInstitutionRequest) {
		return this.post<IResponse<IInstitutionModel>>("HRSetup/Institution/Create", body);
	}

	update(body: IInstitutionRequest) {
		return this.post<IResponse<IInstitutionModel>>("HRSetup/Institution/Edit", body);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IInstitutionModel[]>>("HRSetup/Institution/GetList", {
			params: params,
		});
	}

	getALL() {
		return this.get<IResponse<IInstitutionModel[]>>("HRSetup/Institution/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/Institution/Delete", {
			params: { Id: id },
		});
	}
}
