import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IGovernorateModel, IGovernorateRequest } from "../models/governorate";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { db } from "app/shared/resources/database-tabels";
import { IPageParams } from "app/shared/interfaces/params";
@Injectable({
	providedIn: "root",
})
export class GovernorateService extends AppClientService {
	constructor() {
		super();
	}

	create(body: IGovernorateRequest) {
		return this.post<IResponse<IGovernorateModel>>("HRSetup/Governorate/Create", body);
	}

	update(body: IGovernorateRequest) {
		return this.post<IResponse<IGovernorateModel>>("HRSetup/Governorate/Edit", body);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IGovernorateModel[]>>("HRSetup/Governorate/GetList", {
			params: {
				...params,
				$expand: db.comm_Country,
			},
		});
	}

	getAll() {
		return this.get<IResponse<IGovernorateModel[]>>("HRSetup/Governorate/GetAll");
	}

  getAllByCountryId(countryId: number) {
    return this.get<IResponse<IGovernorateModel[]>>(
      "HRSetup/Governorate/GetAll",
      {
        params: {
          $filter: `countrY_ID eq ${countryId}`,
        },
      }
    );
  }

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/Governorate/Delete", {
			params: { Id: id },
		});
	}
}
