import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import { IAttendancePlanModel, IAttendancePlanRequest, IPlanDetails, IPlan } from "../models/plan";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";

@Injectable({
	providedIn: "root",
})
export class AttendancePlanService extends AppClientService {
	constructor() {
		super();
	}

	create(body: IAttendancePlanRequest) {
		return this.post<IResponse<IAttendancePlanRequest>>("AttendanceSetup/Plan/Create", body);
	}

	update(data: IAttendancePlanRequest) {
		return this.post<IResponse<IAttendancePlanRequest>>("AttendanceSetup/Plan/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IAttendancePlanModel[]>>("AttendanceSetup/Plan/GetList", {
			params: params,
		});
	}

	getById(id: number) {
		return this.get<IResponse<IAttendancePlanModel[]>>("AttendanceSetup/Plan/GetAll", {
			params: {
				$filter: `id eq ${id}`,
			},
		});
	}
	getDetailsById(id: number) {
		return this.get<IPlanDetails>("AttendanceSetup/Plan/Details", {
			params: {
				planId: `${id}`,
			},
		});
	}
  
	getAll() {
		return this.get<IResponse<IAttendancePlanModel[]>>("AttendanceSetup/Plan/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("AttendanceSetup/Plan/Delete", {
			params: { Id: id },
		});
	}

	createExtendWithoutChange(data: IPlan) {
		return this.post<IResponse<IAttendancePlanRequest>>(
			"AttendanceSetup/Plan/ExtendWithoutChange",
			data
		);
	}

	createExtendWithChange(data: IAttendancePlanRequest) {
		return this.post<IResponse<IAttendancePlanRequest>>(
			"AttendanceSetup/Plan/ExtendWithChange",
			data
		);
	}

	createShrink(data: IPlan) {
		return this.post<IResponse<IAttendancePlanRequest>>("AttendanceSetup/Plan/Shrink", data);
	}
}
