import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IIdentityTypeModel,
  IIdentityTypeRequest,
} from "../models/identity-type";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class IdentityTypeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IIdentityTypeRequest) {
    return this.post<IResponse<IIdentityTypeModel>>(
      "HRSetup/IdentityType/Create",
      data
    );
  }

  update(data: IIdentityTypeRequest) {
    return this.post<IResponse<IIdentityTypeModel>>(
      "HRSetup/IdentityType/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IIdentityTypeModel[]>>(
      "HRSetup/IdentityType/GetList",
      {
        params: params,
      }
    );
  }

  getAll() {
    return this.get<IResponse<IIdentityTypeModel[]>>(
      "HRSetup/IdentityType/GetAll"
    ).pipe(
      map((res) => {
        res.data.map((item: IIdentityTypeModel) => {
          item.id = item.identity_TypeID;
          item.ar_Name = item.identity_TypeTital;
          item.en_Name = item.identity_TypeTital;
        });
        return res;
      })
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("HRSetup/IdentityType/Delete", {
      params: { Id: id },
    });
  }
}
