import { Injectable } from "@angular/core";
import { IContractTypeModel, IContractTypeRequest } from "app/hr-setup/models/contract-type";
import { IPageParams } from "app/shared/interfaces/params";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { AppClientService } from "app/shared/services/app-client.service";

@Injectable({
	providedIn: "root",
})
export class ContractTypeService extends AppClientService {
	constructor() {
		super();
	}

	create(data: IContractTypeRequest) {
		return this.post<IResponse<IContractTypeModel>>("HRSetup/contracttype/Create", data);
	}

	update(data: IContractTypeRequest) {
		return this.post<IResponse<IContractTypeModel>>("HRSetup/contracttype/Edit", data);
	}

	getList(params: IPageParams) {
		return this.get<IResponseList<IContractTypeModel[]>>("HRSetup/ContractType/GetList", {
			params: params,
		});
	}

	getAll() {
		return this.get<IResponse<IContractTypeModel[]>>("HRSetup/ContractType/GetAll");
	}

	Delete(id: number) {
		return this.delete<IResponse>("HRSetup/ContractType/Delete", {
			params: { Id: id },
		});
	}
}
