import { Injectable } from "@angular/core";
import { AppClientService } from "app/shared/services/app-client.service";
import {
  IPermissionTypeDropdownResponse,
  IPermissionTypeModel,
  IPermissionTypeRequest,
} from "../models/permissions";
import { IResponse, IResponseList } from "app/shared/interfaces/responces";
import { IPageParams } from "app/shared/interfaces/params";
import { map } from "rxjs/operators";

@Injectable({
  providedIn: "root",
})
export class PermissionTypeService extends AppClientService {
  constructor() {
    super();
  }

  create(data: IPermissionTypeRequest) {
    return this.post<IResponse<IPermissionTypeModel>>(
      "AttendanceSetup/PermissionSetup/Create",
      data
    );
  }

  update(data: IPermissionTypeRequest) {
    return this.post<IResponse<IPermissionTypeModel>>(
      "AttendanceSetup/PermissionSetup/Edit",
      data
    );
  }

  getList(params: IPageParams) {
    return this.get<IResponseList<IPermissionTypeModel[]>>(
      "AttendanceSetup/PermissionSetup/GetList",
      {
        params: params,
      }
    );
  }

  getById(id: number) {
    return this.get<IPermissionTypeDropdownResponse>(
      "AttendanceSetup/PermissionSetup/GetAll",
      {
        params: {
          $filter: `id eq ${id}`,
        },
      }
    );
  }

  getAll() {
    return this.get<IResponse<IPermissionTypeModel[]>>(
      "AttendanceSetup/PermissionSetup/GetAll"
    ).pipe(
      map((res) => {
        res.data.map((item: IPermissionTypeModel) => {
          item.ar_Name = item.name;
          item.en_Name = item.name;
        });
        return res;
      })
    );
  }

  Delete(id: number) {
    return this.delete<IResponse>("AttendanceSetup/PermissionSetup/Delete", {
      params: { Id: id },
    });
  }
}
